.pages-icon-arrow {
  margin: 5px;
  color: rgb(67, 190, 221);
  cursor: pointer;
  width: 30px;
  height: 30px;
  -webkit-transition: color 1s;
  -o-transition: color 1s;
  transition: color 1s;
}

.pages-icon-arrow:hover {
  color: rgb(74, 207, 47);
}


